import React, { Component } from "react";
import {
  Tabs,
  Tab,
  Grid,
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  Button,
  CardMenu,
  IconButton
} from "react-mdl";

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }

  toggleCategories() {
    if (this.state.activeTab === 0) {
      return (
        <div className="projects-grid">
          {/* Project 1 */}
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }} className="projects-row">
            <CardTitle
              style={{
                color: "#03010C",
                height: "176px",
                background: `url(${process.env.PUBLIC_URL}/winko_app.jpeg) center / cover`
              }}
            >
              WINKO APP
            </CardTitle>
            <CardText>
              Es una aplicación para el uso del usario final en el cual pueden
              ver sus estados de cuentas, movimientos, tarjetas asociadas asi
              mismo como diferentes tipos de usuarios y roles
            </CardText>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>

          {/* Project 2 */}
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }} className="projects-row">
            <CardTitle
              style={{
                color: "#03010C",
                height: "176px",
                background: `url(${process.env.PUBLIC_URL}/billypays_app.jpg) center / cover`
              }}
            >
              BILLYPAYS APP
            </CardTitle>
            <CardText>
              Es una aplicación en el cual los usuarios pueden pagar servicios
              de agua, luz, gobierno, recargas
            </CardText>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>

          {/* Project 3 */}
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }} className="projects-row">
            <CardTitle
              style={{
                color: "#03010C",
                height: "176px",
                background:
                  "url(https://xtnotes-1255646395.coshk.myqcloud.com/images/react-1.svg) center / cover"
              }}
            >
              Administrador Billypays
            </CardTitle>
            <CardText>
              Esta plataforma web es para la administración de datos y reportes
              para billypays en ella se administran los servicios brindados en
              la aplicación
            </CardText>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }} className="projects-row">
            <CardTitle
              style={{
                color: "#03010C",
                height: "176px",
                background:
                  "url(https://xtnotes-1255646395.coshk.myqcloud.com/images/react-1.svg) center / cover"
              }}
            >
              Administrador Vuela a la vida
            </CardTitle>
            <CardText>
              En esta plataforma se realizan la administración de tours y
              generación de contratos para los que adquieren dicho servicios
            </CardText>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    } else if (this.state.activeTab === 1) {
      return (
        <div>
          <h1>This is Angular</h1>
        </div>
      );
    } else if (this.state.activeTab === 2) {
      return (
        <div>
          <h1>This is VueJS</h1>
        </div>
      );
    } else if (this.state.activeTab === 3) {
      return (
        <div>
          <h1>This is MongoDB</h1>
        </div>
      );
    }
  }

  render() {
    return (
      <div>
        <Tabs
          activeTab={this.state.activeTab}
          onChange={tabId => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>React Js y React Native</Tab>
        </Tabs>

        <Grid>
          <Cell col={12}>
            <div className="content">{this.toggleCategories()}</div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Projects;



