import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";
import Education from "./education";
import Experience from "./experience";
import Skills from "./skills";

class Resume extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Cell col={4}>
            <div style={{ textAlign: "center" }}>
              <img
                src={`${process.env.PUBLIC_URL}/profile.jpg`}
                alt="avatar"
                style={{ height: "200px", borderRadius: "100%" }}
              />
            </div>

            <h2 style={{ paddingTop: "2em" }}>Juan Pablo</h2>
            <h4 style={{ color: "grey" }}>Programador</h4>
            <hr style={{ borderTop: "3px solid #833fb2", width: "50%" }} />
            <p>
              Me apaciona lo que tenga que ver con tecnolgia en especial el area de 
              desarrollo de software. me gusta este trabajo ya que en el puedo expresar
              mis ideas puedo aportar soluciones a un problema. Actual mente llevo +5 años de
              experecia en el desarrollo de sistemas web 
            </p>
            <hr style={{ borderTop: "3px solid #833fb2", width: "50%" }} />
            <h5>Dirección</h5>
            <p>
              Col. Lomas de Tepeolulco, Tlalnepantla de Baz Estadi de México
            </p>
            <h5>Telefono</h5>
            <p>+52 55-74-64-18.52</p>
            <h5>Correo</h5>
            <p>juan_pablo_ing@outlook.es</p>
            <hr style={{ borderTop: "3px solid #833fb2", width: "50%" }} />
          </Cell>
          <Cell className="resume-right-col" col={8}>
            <h2>Educación</h2>

            <Education
              startYear={2013}
              endYear={2015}
              schoolName="Universidad Tecnologica de Xicotepec de Juarez"
              schoolDescription="Ingenieria de Tecnologias de la Información Area Sistemas Informaticos"
            />

            <Education
              startYear={2010}
              endYear={2013}
              schoolName="Universidad Tecnologica de Xicotepec de Juarez"
              schoolDescription="Tecnico Superior Universitario en Tecnologías de la Información Area Sistemas Informaticos"
            />
            <hr style={{ borderTop: "3px solid #e22947" }} />

            <h2>Experiencia</h2>

            <Experience
              startYear={2019}
              endYear={2020}
              jobName="Hybridos Studio"
              jobDescription="En hybridos studios llevo un par de años trabajando como freenlace despues de mi renucnia en swplus me consolido como tiempo completo colarobando en nuevos desarrollos 
              de aplicaciones mobil y plataformas administrativas ocupando diferentes tipos de lenguajes y framework"
            />

            <Experience
              startYear={2015}
              endYear={2019}
              jobName="SWPLUS"
              jobDescription="En swplus fue mi primer trabajo como desarrollador de software donde me enfoque mas al backend durante los primeros 2 años. Durante los 2 ultimos años
               me dedique a aprender frontend  con framework como reactjs, angularjs. En mi estancia en swplus participe en diferentes proyectos que integraban funciones como administración
               de pagos, facturación, administración de expedientes clinicos electronicos. Tambien desarrolle aplicaciónes hybridas ocupando framework IONIC"
            />
            <hr style={{ borderTop: "3px solid #e22947" }} />
            <h2>Skills</h2>
            <Skills skill="javascript" progress={100} />
            <Skills skill="HTML/CSS" progress={80} />
            <Skills skill="C#" progress={85} />
            <Skills skill="ReactJs" progress={85} />
            <Skills skill="AngularJs" progress={85} />
            <Skills skill="React Native" progress={85} />
            <Skills skill="Ionic" progress={85} />
            <Skills skill="SQL Server" progress={85} />
            <Skills skill="MySQL" progress={85} />
            <Skills skill=".Net Core" progress={85} />
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Resume;



